import AWS from 'aws-sdk';
let sts = new AWS.STS({});

export const getSecureTokensFromSTS = async (idToken) => {
    const config = {
        RoleArn: 'arn:aws:iam::164535879341:role/church-bulletin-admin-role',
        RoleSessionName: 'church-bulletin-admin-session',
        WebIdentityToken: idToken
    };
    await new Promise((resolve, reject) => {
        sts.assumeRoleWithWebIdentity(config, (err, data) => {
            if(err) {
                console.log('err calling sts: ', err);
                return reject(err);
            }
            if(data) {
                console.log('sts data: ', data);
                return resolve(data);
            }
        });
    });
}
